let moneyBefore = []
let showtu3 = []
let moneyAfter = []
class Dashboard extends Component {
  componentWillMount() {
    showtu3 = []
    var xmlhttp;
    if (window.XMLHttpRequest)
    {
      //  IE7+, Firefox, Chrome, Opera, Safari 浏览器执行代码
      xmlhttp=new XMLHttpRequest();
    }
    xmlhttp.onreadystatechange=function()
    {
      if (xmlhttp.readyState==4 && xmlhttp.status==200)
      {
        moneyBefore = JSON.parse(xmlhttp.responseText)
      }
    }

    xmlhttp.open("GET","/electric/charge",false);
    xmlhttp.send();
    var xmlhttp;
    if (window.XMLHttpRequest)
    {
      //  IE7+, Firefox, Chrome, Opera, Safari 浏览器执行代码
      xmlhttp=new XMLHttpRequest();
    }
    xmlhttp.onreadystatechange=function()
    {
      if (xmlhttp.readyState==4 && xmlhttp.status==200)
      {
        moneyAfter = JSON.parse(xmlhttp.responseText)
      }
    }

    xmlhttp.open("GET","/electric/charge2",false);
    xmlhttp.send();
    var myDate = new Date();
    var ltime = Math.floor(myDate.getTime()/(1000*60*60*24))*(1000*60*60*24)-8*1000*60*60;
    for (var ii = 0 ; ii<10; ii++)
    {
      var myDate1 = new Date(ltime- (9-ii) * (1000*60*60*24));
      showtu3.push(myDate1.toLocaleString());
    }
  }
  render() {
    const mainChart1 = {
      labels: showtu3,
      datasets: [
        {
          label: 'charge in',
          backgroundColor: hexToRgba(brandInfo, 10),
          borderColor: brandInfo,
          pointHoverBackgroundColor: '#fff',
          borderWidth: 2,
          borderCapStyle: 'butt',
          borderDash: [],
          borderDashOffset: 0.0,
          borderJoinStyle: 'miter',
          pointBorderColor: 'rgba(251,227,48,1)',
          pointBackgroundColor: '#fff',
          pointBorderWidth: 1,
          pointHoverRadius: 5,
          pointHoverBorderColor: 'rgba(220,220,220,1)',
          pointHoverBorderWidth: 3,
          pointRadius: 2,
          pointHitRadius: 10,
          scaleShowGridLines : true,
          data: moneyBefore,
        },
        {
          label: 'charge out',
          backgroundColor: 'transparent',
          borderColor: brandSuccess,
          pointHoverBackgroundColor: '#fff',
          borderWidth: 2,
          borderCapStyle: 'butt',
          borderDash: [],
          borderDashOffset: 0.0,
          borderJoinStyle: 'miter',
          pointBorderColor: 'rgba(251,227,48,1)',
          pointBackgroundColor: '#fff',
          pointBorderWidth: 1,
          pointHoverRadius: 5,
          pointHoverBorderColor: 'rgba(220,220,220,1)',
          pointHoverBorderWidth: 3,
          pointRadius: 2,
          pointHitRadius: 10,
          scaleShowGridLines : true,
          data: moneyAfter,
        },
      ],
    };

    const mainChartOpts1 = {
      tooltips: {
        enabled: false,
        custom: CustomTooltips,
        intersect: true,
        mode: 'index',
        position: 'nearest',
        callbacks: {
          labelColor: function(tooltipItem, chart) {
            return { backgroundColor: chart.data.datasets[tooltipItem.datasetIndex].borderColor }
          }
        }
      },
      maintainAspectRatio: false,
      elements: {
        point: {
          radius: 0,
          hitRadius: 10,
          hoverRadius: 4,
          hoverBorderWidth: 3,
        },
      }
    };

    let total_before = 0;
    let total_after = 0;
    for(let i = 0; i < moneyBefore.length; i++) {
      total_before += moneyBefore[i];
      total_after += moneyAfter[i];
    }
    let rate = ((total_before) / total_after).toFixed(2) * 100;

    return(
      <Row>
        <Col>
          <Card>
            <CardBody>
              <Row>
                <Col sm="5">
                  <CardTitle className="mb-0">charge</CardTitle>
                  <div className="small text-muted">Recent 10 days</div>
                </Col>
              </Row>
              <div className="chart-wrapper" style={{ height: 300 + 'px', marginTop: 40 + 'px' }}>
                <Line data={mainChart1}  options={mainChartOpts1} height={300} />
              </div>
            </CardBody>
            <CardFooter>
              <Row className="text-center">
                <Col sm={12} md className="mb-sm-2 mb-0">
                  <div className="text-muted">check in</div>
                  <strong>{total_before}</strong>
                </Col>
                <Col sm={12} md className="mb-sm-2 mb-0">
                  <div className="text-muted">check out</div>
                  <strong>{total_after}</strong>
                </Col>
                <Col sm={12} md className="mb-sm-2 mb-0">
                  <div className="text-muted">Money Save</div>
                  <strong>{total_before} ({rate}%)</strong>
                  <Progress className="progress-xs mt-2" color="success" value={rate} />
                </Col>
              </Row>
            </CardFooter>
          </Card>
        </Col>
      </Row>
    );
  }
}



















